import React, { useState, useEffect } from "react";
import axios from 'axios';
import { Container, Row, Col, Figure } from "react-bootstrap";


function ProductDetail({ match }){

    const [products, setProduct] = useState({})
    const [post, setPost] = useState({})
    const id = match.params.id

    useEffect(() => {
        axios({
            method: "GET",
            url: `https://screw23.herokuapp.com/api/products_product/${id}/`
        }).then(response => {
            setPost(response.data)
            setProduct(response.data.product)
        })
    }, [id])

    return (
        <Container>
            <Row>
                <Col>
                <Figure>
                  <Figure.Image
                    width={256}
                    height={512}
                    src={post.image}
                  />
                  <Figure.Caption>
                      {post.name}
                  </Figure.Caption>
                </Figure>
                </Col>
                <Col>
                    <Row>
                        <Col>
                            <h3>Description:</h3>
                        </Col>
                        <Col>
                            <h5>{post.description_1}</h5>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <h3>Description:</h3>
                        </Col>
                        <Col>
                            <h5>{post.description_2}</h5>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <h3>Description:</h3>
                        </Col>
                        <Col>
                            <h5>{post.description_3}</h5>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <h3>Description:</h3>
                        </Col>
                        <Col>
                            <h5>{post.description_4}</h5>
                        </Col>
                    </Row>
                </Col>
            </Row>
            <br/>
             <hr/>
            <Row>
                <h5>          ,
                    API      "api/products_product/{id}",
                    API         ,
                         front-end   :(
                             
                    </h5>
            </Row>
            <Row>
                {products.map(p=>(
                <h5>
                    {products.name}
                </h5>
                    ))}
            </Row>
        </Container>

    )
}

export default ProductDetail;