import React, {useState, useEffect} from "react";
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Container, Row, Col, Button, Dropdown, ButtonGroup} from 'react-bootstrap'


function MyNavbar(){

    const [subcategory, setSubCategory] = useState([])
    const [category, setCategory] = useState([])

    useEffect(()=> {
        axios({
            method: "GET",
            url: `http://localhost:8000/api/categories/`
        }).then(response => {
            setCategory(response.data)
            setSubCategory(response.data.sub_categories)
        })
    }, [])
    return (
        <div className="App">
            <Container>
                <br/>
              <Row>
                <Col/>
                <Col xs={4}>
                    <h1>Screw Shop</h1>
                </Col>
                <Col className="col-4 d-flex justify-content-end align-items-center">
                    <Button variant="outline-secondary">Sign up</Button>{' '}
                </Col>
              </Row>
                <hr/>
            </Container>
            <Container >
                    <Row>
                        <div className="position-sticky" style={{"top":"2rem"}}>
                        <Col>
                            <div className="nav-scroller py-1 mb-2">
                                <nav className="nav d-flex justify-content-between">
                                    <Link  className="p-2 link-secondary" to={{ pathname: `/`, fromDashboard: false }}><h6><u>Home</u></h6></Link>
                                        <Dropdown as={ButtonGroup}>
                                            {category.map(c=>(
                                          <Button variant="success">{c.name}</Button>
                                            ))}
                                          <Dropdown.Toggle split variant="success" id="dropdown-split-basic" />

                                          <Dropdown.Menu>
                                              {subcategory.map(c => (
                                            <Dropdown.Item href="#/action-1">{c.name}</Dropdown.Item>
                                                  ))}
                                          </Dropdown.Menu>
                                        </Dropdown>
                                </nav>
                                <hr/>
                            </div>
                        </Col>
                        </div>
                    </Row>
                <Row>
                </Row>
            </Container>
        </div>
    );
}

export default MyNavbar;